<template>
  <div class="member_center">
    <div class="membership_grade">
      <div class="title">我的会员等级</div>
      <div class="membership_grade_box">
        <el-row type="flex" class="row_size">
          <img class="img_vip" src="../../assets/images/vip.png">
          <div class="text_container">
            <ul>
              <li>VIP会员</li>
              <li>使用期限延长至1年</li>
              <li>互联网基础数据可查询量相比普通用户提升10倍——500次/天/账户；工具类数据接口可查询量相比普通用户提升20倍——200次/天/账户
                完善的用户管理功能：单位用户的登录日志、浏览记录、查询数据量统计、数据查询管控</li>
            </ul>
          </div>
        </el-row>
      </div>
    </div>

    <!-- 购买记录列表 -->
    <div class="recharge_record">
      <div class="title">充值记录</div>
      <div class="container borderRadius">
        <el-table class="recharge_record_table" :data="userRechargeRecord" border style="width: 100%" ref="multipleTable">
          <el-table-column prop="id" label="序号" type="index" width="" align="center"></el-table-column>
          <el-table-column prop="rechargePackageName" label="购买的数据套餐" align="center"></el-table-column>
          <el-table-column prop="resourceName" label="购买的数据模块" align="center"></el-table-column>
          <el-table-column prop="chargingCount" label="购买次数" align="center"></el-table-column>
          <el-table-column prop="money" label="购买金额" align="center"></el-table-column>
          <el-table-column prop="chargingTime" label="购买时间" align="center"></el-table-column>
        </el-table>

        <!-- 翻页 -->
        <div class="pagination">
          <el-pagination @current-change="handleCurrentChange" background  :page-size="pageSize" layout="prev, pager, next" :total="totalRowNum"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    data () {
      return {
        page: 1,
        pageSize: 2,
        totalRowNum: 0,
        userRechargeRecord: []
      }
    },
    mounted () {
      this.getUserIdentityLevel()
      this.getUserRechargeRecord()
    },
    methods: {
      handleCurrentChange (val) {
        this.page = val
        this.getUserRechargeRecord()
      },
      getUserIdentityLevel () {
        gapi.getReadJudgeVIP()
          .then((res) => {
            console.log(res)
            if (res.data.code === 1) {
              // console.log(res, '1')
            }
          }).catch((err) => {
          console.log(err)
        })
      },
      getUserRechargeRecord () {
        let pram = {
          currentPageNum: ''+this.page,
          rowNumPerPage: ''+this.pageSize
        }
        console.log(pram)
        gapi.getReadChargingLoggerAll(pram)
          .then((res) => {
            if (res.data.code === 1) {
              this.totalRowNum = res.data.data.totalRowNum
              this.userRechargeRecord = res.data.data.resultList
              console.log(this.totalRowNum)
              console.log(this.userRechargeRecord)
              // console.log(res, '2')
            }
          }).catch((err) => {
          console.log(err)
        })
      }
    }
  }
</script>

<style>
  .member_center {
    margin: 0 auto;
  }
  .member_center .title {
    font-size: 18px;
    font-family: MicrosoftYaHei;
    color: rgba(46,46,48,1);
    line-height: 30px;
    margin: 0 0 15px;
    text-align: left;
    font-weight: bold;
  }

  .member_center .membership_grade_box {
    padding: 30px 18px;
    background: rgba(255,255,255,1);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(6,0,1,0.1);
    margin-bottom: 20px;
  }

  .member_center .recharge_record .borderRadius {
    box-shadow: 0 0 10px 2px rgba(6,0,1,0.1);
    border-radius: 10px;
    padding: 0;
  }
  .member_center .recharge_record .borderRadius .recharge_record_table {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border: none;
  }

  .member_center .recharge_record .borderRadius .recharge_record_table .el-table__header .cell {
    background: rgba(7,134,223,1);
    color: rgba(255,255,255,1);
    font-size: 16px;
    font-weight: normal;
  }

  .img_vip {
    width: 70px;
    height: 70px;
  }

  li {
    font-size: 16px;
    color: #909090;
  }

  .text_container {
    display: flex;
    flex: 1;
    margin-left: 20px;
  }

</style>



